import fetchJsonp from './async'
import { client as yh } from '../js/client.js';

var HTTPUtil = {}

HTTPUtil.get = function (url, successfn, failfn) {
  console.log('url:' + url)
  $.ajax({
    type: 'GET',
    url: url,
    async: true,
    success: successfn,
    error: failfn
  })
}
