HTTPUtil.post = function (url, data, successfn) {
  $.post(url, data, successfn);
}

HTTPUtil.jsonp = function (url, successfn, errorfn) {
  fetchJsonp(
    url,
    {
      jsonpCallback: 'jsonpCallback'
    },
    successfn,
    errorfn
  )
}

