/***
 *  CDN
 */
yh.prototype.getCdn = function (districtCode) {
  let cdn = ''
  for (let index = 0; index < custInfo.length; index++) {
    const element = custInfo[index]
    if (element.districtCode == districtCode) {
      cdn = element.cdn
      break
    }
  }
  return cdn
}

