yh.prototype.addHistory = function (historyData) { //  () ---wzh
  let historyList = localStorage.getItem('historyListsr');
  if (historyList) {
    historyList = JSON.parse(historyList)
    historyList = historyList.filter(arr => arr.relateId !== historyData.relateId)
    historyList.unshift(historyData);
  } else {
    historyList = []
    historyList.unshift(historyData);
  }
  console.log('');
  console.log(historyList);
  localStorage.setItem('historyListsr', JSON.stringify(historyList));
}
yh.prototype.delHistory = function (index) { // ---wzh
  let historyList = JSON.parse(localStorage.getItem('historyListsr'));
  if (historyList.length > 0) {
    historyList.splice(index, 1)
    console.log('');
    console.log(historyList);
    localStorage.setItem('historyListsr', JSON.stringify(historyList));
  } else {
    console.log('');
  }
},
yh.prototype.delAllHistory = function () { // ---wzh
  localStorage.removeItem('historyListsr')
}
